/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Christopher Teaches Chess","date":"22 Dec 2007","slug":"/2007-12-22-christopher-teaches-chess/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2127838175\" title=\"View 'CIMG0410' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2111/2127838175_9c64d12414.jpg\" alt=\"CIMG0410\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Christopher is teaching Mama to play chess this morning.</p>"},"next":{"fields":{"title":"POTS VPN","date":"17 Dec 2007","slug":"/2007-12-17-pots-vpn/"}},"previous":{"fields":{"title":"Mary and Joseph","date":"23 Dec 2007","slug":"/2007-12-23-mary-and-joseph/"}}},{"node":{"fields":{"title":"POTS VPN","date":"17 Dec 2007","slug":"/2007-12-17-pots-vpn/","tags":"Y2007"},"html":"<p>Now that we finally got DSL back after 10 days down, it turns out the <a href=\"http://www.t-systems.com/tsi/en/30948/Home/LargeEnterprise/Solutions/Telecommunications/TVPN/1-TVPN\">T-VPN</a> we are joining is a voice-service VPN, not a data VPN. I guess the Deutsche Telekom uses the term VPN differently than the rest of the world, which was confusing me (not to mention the fact that the whole transaction had to wait for our DSL line).</p>\n<p><a href=\"http://mcdonalds.de/html.php?t=McCaf%E9%AE&amp;c=mccafe_wlan\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/12/wlan.jpg\" alt=\"wlan.jpg\" border=\"0\" width=\"180\" height=\"211\" align=\"right\" /></a>Not that a POTS (plain old telephone service) VPN isn't a cool thing, it's just not what I was expecting.</p>\n<p>It's also cool that our other Telekom issues at the restaurant were both resolved today. We once again have a T-Mobile HotSpot (with <a href=\"http://mcdonalds.de/html.php?t=McCaf%E9%AE&amp;c=mccafe_wlan\">an hour free WLAN every day!</a>) and we have a DSL line for our flat screen TV.</p>"},"next":{"fields":{"title":"December Surprise","date":"17 Dec 2007","slug":"/2007-12-17-december-surprise/"}},"previous":{"fields":{"title":"Christopher Teaches Chess","date":"22 Dec 2007","slug":"/2007-12-22-christopher-teaches-chess/"}}},{"node":{"fields":{"title":"December Surprise","date":"17 Dec 2007","slug":"/2007-12-17-december-surprise/","tags":"Y2007"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2117336884\" title=\"View 'card 2007' on Flickr.com\"><img src=\"https://farm3.static.flickr.com/2239/2117336884_495c07304e_m.jpg\" alt=\"card 2007\" border=\"0\" width=\"171\" height=\"240\" align=\"right\" /></a> We're absolutely unprepared for Christmas this year (it always comes so unexpectedly every December). At work we've set the Christmas party for the end of January and are calling it a \"End of Year\" party. People seem to have more time in for Christmas parties in January anyway.</p>\n<p>At least I got our photo cards ready for our friends and relatives in the States (as always at the <a href=\"http://www.usps.com/cardstore/\">USPS Card Store</a>, where they mail the cards for us at domestic rates). Nothing new in the card if you read PapaScott regularly, but I did come up with a couple of good lines...</p>\n<blockquote><p>\n  After 24 years of Frauke working for McDonald's, McDonald's now works for us!\n</p></blockquote>\n<p>(Almost sounds like a Soviet Russia joke. :-) )</p>\n<blockquote><p>\n  (Christopher) keeps reminding us what is really important, and makes our lives a joy (even if it is sometimes a little messy).\n</p></blockquote>\n<p>I now notice I have at least one typo in the final version of the printed card. That's the nice thing about the internet... you can always go back and correct your speling.</p>"},"next":{"fields":{"title":"Monomedia","date":"16 Dec 2007","slug":"/2007-12-16-monomedia/"}},"previous":{"fields":{"title":"POTS VPN","date":"17 Dec 2007","slug":"/2007-12-17-pots-vpn/"}}}],"pathPrefix":"","first":false,"last":false,"index":91,"pageCount":1101}}

/***/ })

